<template>
  <div>
    <el-table
    ref="multipleTable"
    :data="tableData"
    tooltip-effect="dark"
    :header-cell-class-name="cellClass"
    style="width: 100%"
    @select="handleSelect"
    @selection-change="handleSelectionChange">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
      label="日期"
      width="120">
      <template slot-scope="scope">{{ scope.row.date }}</template>
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="120">
    </el-table-column>
    <el-table-column
      prop="address"
      label="地址"
      show-overflow-tooltip>
    </el-table-column>
  </el-table>
  </div>
  
 </template>
 
 <script>
  export default {
    data() {
      return {
        tableData: [{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }],
        multipleSelection: [],
        currentRow:{}
      }
    },
 
    methods: {
      //多选改造单选
      handleSelect(selection, row) {
          if (selection.length > 1) {
            console.log(this.$refs.multipleTable)
            this.$refs.multipleTable.clearSelection();
            this.$refs.multipleTable.toggleRowSelection(selection.pop());
          } else {
            this.multipleSelection = selection;
          }
    
          this.setCurrentRow(row);
      },
      // 点击操作一栏设置当前操作行数据
       setCurrentRow(row) {
         this.currentRow = row;
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }
    }
  }
 </script>
 <style>
 .el-table /deep/.disabledCheck > .cell .el-checkbox__inner{
    display: none !important;
    position: relative;
 }
 .el-table /deep/.disabledCheck > .cell::before{
    display: block;
    content:"";
    position: absolute;
    text-align: center;
    width: 100%;
 }
 .el-table__header-wrapper  .el-checkbox{
	display:none
}
.el-table__empty-block{
  display: none;
}
 </style>
 
 